body:has(~ #site-navigation.active) {
  overflow: hidden;
}

header.site-header {
  padding: 41px 0;
  border-bottom: 5px solid $c-brand;

  @include tablet {
    padding: 18px 0;
  }

  .content-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .site-logo {
      z-index: 10;

      &:has(~ #site-navigation.active) {

      }
    }

    .menu-toggle {
      display: none;

      @include tablet {
        display: flex;
        align-items: center;
        gap: 10px;
        z-index: 10;
        padding: 10px 12px;
        border: 2px solid $c-blue;
        border-radius: 6px;
        background: $c-white;
        transition: .3s all;

        &:has(~ #site-navigation.active) {
          background: transparent;

          * {
            opacity: 0;
          }

          .toggle-icon {
            opacity: 1;
            border-color: $c-white;
            border-bottom: 0;

            &:before,
            &:after {
              background: $c-white;
              bottom: 0;
              margin: auto;
            }

            &:before {
              transform: rotate(135deg);
            }

            &:after {
              transform: rotate(45deg);
            }
          }
        }

        .toggle-text {
          font-size: 18px;
          font-weight: 700;
          color: $c-blue;
        }

        .toggle-icon {
          position: relative;
          display: block;
          width: 25px;
          height: 17px;
          border-bottom: 3px solid $c-blue;
          transition: .3s all;

          &:before,
          &:after {
            content: '';
            height: 3px;
            background: $c-blue;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            transition: .3s all;
          }

          &:after {
            bottom: -3px;
            margin: auto;
          }
        }
      }
    }

    nav {
      @include tablet {
        position: absolute;
        height: 100vh;
        overflow: scroll;
        top: 0;
        right: 0;
        max-width: 0;
        width: 100%;
        background: $c-blue;
        transition: .3s all;

        &.active {
          padding: 150px 35px 35px;
          max-width: 100vw;
        }
      }

      ul {
        list-style: none;
        padding: 0;
        margin: 0;

        a {
          @include tablet {
            color: $c-white;
            font-size: 25px;
          }

          &:hover {
            text-decoration: none;
          }
        }
      }

      #header-menu-desktop {
        display: flex;
        align-items: center;
        flex-direction: row;
        gap: 30px;
        font-weight: 700;
        font-size: 18px;

        @include tablet {
          flex-direction: column;
        }
        .btn{
          background-color: transparent;
          border-color: #0A314E;
        }
      }
    }
  }
}
