@import "../../assets/scss/variable";

.footer {
  background: $c-blue;
  border-top: 8px solid $c-brand;
  padding: 50px 0 70px;

  @include mobile {
    padding: 40px 0 15px;
  }

  &-content {
    &__top {
      display: grid;
      grid-template-columns: 1fr 2fr 1fr;
      gap: 40px;
      margin-bottom: 15px;
      color: $c-white;

      @include mobile {
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
          "middle middle"
          "left right";
        row-gap: 30px;
        column-gap: 5px;
      }

      > div {
        @include mobile {
          grid-area: left;
        }

        h3 {
          font-size: 20px;
          font-weight: 700;
          margin-bottom: 0;
          color: $c-brand;
        }

        a {
          font-size: 14px;
          color: $c-white;

          &:hover {
            text-decoration: none;
            color: $c-brand;
          }
        }
      }

      .bordered-text {
        text-align: center;
        padding: 11px;
        border: 2px solid $c-brand;
        border-radius: 6px;
        align-items: flex-start;
        height: fit-content;
        width: fit-content;
        margin: auto;

        @include mobile {
          grid-area: middle;
        }

        h3 {
          font-size: 32px;
          margin: 0;
          line-height: 1;
          color: $c-white;

          span {
            color: $c-brand;
          }
        }
      }

      > a {
        display: flex;
        align-items: flex-end;
        justify-content: flex-end;

        @include mobile {
          grid-area: right;
        }
      }
    }

    &__separator {
      width: 100%;
      height: 30px;
      border: 4px solid $c-white;
      border-top: 0;
      margin-bottom: 26px;

      @include mobile {
        display: none;
      }
    }

    &__bottom {
      display: flex;
      justify-content: space-between;
      gap: 30px;

      @include mobile {
        flex-direction: column-reverse;
        gap: 16px;
        text-align: center;
      }

      div {
        display: flex;
        gap: 30px;
        font-size: 10px;
        color: $c-white;

        @include mobile {
          flex-direction: column;
          font-size: 13px;
          gap: 18px;
        }

        ul {
          padding: 0;
          margin: 0;
          list-style: none;
          display: flex;
          align-items: center;
          gap: 30px;

          @include tablet {
            flex-direction: column;
            gap: 5px;
            align-items: flex-start;
            justify-content: center;
          }

          @include mobile {
            flex-direction: row;
            align-items: center;
            gap: 30px;
          }

          li {
            white-space: nowrap;

            a {
              color: $c-white;
              text-decoration: underline;
            }
          }
        }

        p {
          font-size: 10px;

          @include mobile {
            font-size: 13px;
            text-align: left;
            margin: 0;
          }
        }
      }

      img {
        @include mobile {
          max-width: 250px;
          width: 100%;
          margin: auto;
        }
      }
    }
  }
}
