@import "_variable";


h1, .h1 {
  font-size: clamp(2rem, 5vw, 4rem);
  line-height: 1.2;
  font-weight: 700;
}

h2, .h2 {
  font-size: clamp(1.75rem, 3vw, 2.5rem);
  line-height: 1.05;
  font-weight: 500;
}

h3, .h3 {
  font-size: 1.125rem;
  line-height: 1.55556; // 28px
  font-weight: 600;
}

h4, .h4 {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  margin: 1em 0 1rem;
  @include mobile {
    font-size: 20px;
    line-height: 1.3;
  }
}

h5, .h5 {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.2;
  margin: 1em 0 1rem;
  @include mobile {
    font-size: 10px;
    line-height: 1.3;
  }
}

h6, .h6 {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  margin: 1em 0 1rem;
  @include mobile {
    font-size: 14px;
    line-height: 1.3;
  }
}

p, .p {
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 122%;
  @include tablet-lg {
    font-size: 1rem;
  }
}

.radius {
  border-radius: 12px;
  @include mobile {
    border-radius: 10px;
  }
}

