.wp-block-button .wp-element-button,
.btn {
  display: inline;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  font-size: 18px;
  line-height: 100%;
  padding: 10px 13px;
  text-decoration: none;
  background-color: $c-brand;
  color: $c-white;
  cursor: pointer;
  position: relative;
  font-weight: 700;
  font-family: $main-font;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  transition: all 300ms linear;
  text-transform: capitalize;
  border: 2px solid $c-brand;
  border-radius: 6px;

  &:hover {
    transition: all 300ms linear;
    background-color: $c-white;
    color: $c-brand;

  }

  @include tablet {
    font-size: 18px;
  }

  &__text {
    position: relative;

  }

  &__icon {
    position: relative;
    line-height: 1;
    transition: all 300ms linear 0s;

    &--after {
      order: 2;
    }

    &--before {
      order: -1;
    }

    &:hover {
      transition: all 300ms linear;

    }

    svg {
      fill: currentColor;
      vertical-align: top;
    }

    img {
      width: 30px;
      height: 30px;
      object-fit: contain;
      vertical-align: top;
    }
  }

  &--icon {
    padding-right: 8px;

    .svg-icon {
      width: 28px;
      height: 28px;
      padding: 9px 7px;
      border: 1px solid;
      display: flex;
      position: relative;
      align-items: center;
      justify-content: center;
      stroke: currentColor;
      border-radius: $border-rad;
    }

    &.rotate {
      &.active {
        svg {
          transform: rotate(45deg);
        }
      }

      &.inactive {
        svg {
          transform: rotate(-45deg);
        }
      }
    }

    &-only {
      padding: 0px;

      &.inactive {
        background-color: $c-brand;
        border-color: $c-brand;
        color: #fff;

        &:hover {
          background-color: $c-brand;
          border-color: $c-brand;
          color: $c-dark;
        }
      }

      &.active {
        background-color: $c-brand;
        border-color: $c-dark;
        color: $c-white;

        &:hover {
          background-color: $c-brand;
          border-color: $c-brand;
          color: $c-dark;
        }
      }

      .svg-icon {
        border: 0px;
        transition: all 300ms linear;
      }
    }

  }

  &--primary {
    background-color: $c-white;
    border-color: $c-brand;
    color: $c-brand;

    .svg-icon {
      background-color: $c-white;
      color: $c-brand;
      border-color: $c-brand;
      transition: all 300ms linear;
    }

    &:hover {
      background-color: $c-brand;
      border-color: $c-brand;

      color: $c-dark;

      .svg-icon {

        background-color: transparent;
        border-color: $c-dark;
        color: $c-dark;

      }

    }

    &-dark {
      background-color: $c-light-gray;
      color: $c-dark;

      .svg-icon {
        background-color: $c-brand;
        stroke: $c-light-gray;
        border-color: $c-brand;
      }

      &:hover {
        background-color: $c-brand;
        color: $c-dark;

        .svg-icon {

          background-color: transparent;
          border-color: $c-dark;
          stroke: $c-dark;

        }

      }

    }
  }

  &--secondary {
    background-color: #E8E0D1;
    border-color: #E8E0D1;
    color: $c-dark;

    .svg-icon {
      background-color: $c-brand;
      color: $c-light-gray;
      border-color: $c-brand;
      transition: all 300ms linear;
    }

    &:hover {
      background-color: $c-brand;
      border-color: $c-brand;

      color: $c-dark;

      .svg-icon {

        background-color: transparent;
        border-color: $c-dark;
        color: $c-dark;

      }

    }
  }

  &--reverse {
    .svg-icon {
      transform: rotate(180deg);
    }
  }


  .btn__icon:last-child:has(.icon-chevron-right) {
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      height: 3px;
      right: 6px;
      border-radius: 1px;
      width: 0;
      margin-top: -1.5px;
      background-color: currentColor;
      transition: width 300ms ease 0s;
    }
  }

  &:hover {
    text-decoration: none;

    .btn__icon:last-child:has(.icon-chevron-right) {
      transform: translateX(6px);

      &:before {
        width: 16px;
      }
    }
  }

  &-mobile-only {
    @include mf-tablet {
      display: none !important;
    }
  }
}

.wp-block-button {
  margin-top: 40px;
  padding-bottom: 10px;

  &.is-style-outline {
    .wp-element-button {
      background: transparent;
      border-color: $c-brand;
      color: $c-white;
      font-size: 1.25rem;

      mark {
        transition: .3s ease;
      }

      &:hover {
        background: $c-brand;
        color: $c-white;

        mark {
          color: $c-white !important;
        }
      }
    }
  }
}

a {
  color: $c-dark;
}

.btn-group {
  --gap: 24px;
  display: flex;
  gap: var(--gap);
  flex-wrap: wrap;

  &--vertical {
    flex-direction: column;
    // &.btn-group--center {
    // 	text-align: center;
    // }
    // &.btn-group--left {
    // 	text-align: left;
    // }
    // &.btn-group--right {
    // 	text-align: right;
    // }
  }

  &--center {
    justify-content: center;
    text-align: center;
  }

  &--left {
    justify-content: flex-start;
    text-align: left;
  }

  &--right {
    justify-content: flex-end;
    text-align: right;
  }
}

.responsive-embed {
  border-radius: 10px;
  overflow: clip;
}

.position-sticky {
  position: sticky;
}

.align-center {
  align-items: center;
}

.desktop-only {
  @include tablet {
    display: none !important;
  }
}

.mobile-only {
  @include mf-desktop {
    display: none !important;
  }
}

@include mobile {
  .mobile-order-1 {
    order: 1;
  }
  .mobile-order-2 {
    order: 2;
  }
  .mobile-order-3 {
    order: 3;
  }
}
