@mixin desktop-lg {
	@media only screen and (max-width: 1800px) {
		@content;
	}
}
@mixin desktop {
	@media only screen and (max-width: 1400px) {
		@content;
	}
}
@mixin tablet-lg {
	@media only screen and (max-width: 1280px) {
		@content;
	}
}
@mixin tablet {
	@media only screen and (max-width: 1024px) {
		@content;
	}
}

@mixin mobile {
	@media only screen and (max-width: 768px) {
		@content;
	}
}
@mixin mobile-sm {
	@media only screen and (max-width: 575px) {
		@content;
	}
}
@mixin height-sm {
	@media only screen and (max-height: 630px) {
		@content;
	}
}

@mixin mf-desktop {
	@media only screen and (min-width: 1025px) {
		@content;
	}
}
@mixin mf-tablet {
	@media only screen and (min-width: 768px) {
		@content;
	}
}

$margin-sizes: (
  180: 90,
  120: 60,
  100: 50,
  80: 30,
  60: 20,
  40: 16,
  30: 16,
  20: 16
);

// Function to generate margin classes
@mixin generate-margin-classes($type) {
	@each $size, $mobile-size in $margin-sizes {
		.#{$type}--#{$size} {
		@if $type == 'mt' {
			margin-top: #{$size}px;
			@include mobile {
			margin-top: #{$mobile-size}px;
			}
		} @else if $type == 'mb' {
			margin-bottom: #{$size}px;
			@include mobile {
			margin-bottom: #{$mobile-size}px;
			}
		}
		}
	}
}



