  html {
    --scrollbar-width: 0px !important;
  }
}

body {
  min-width: 320px;
  font: 16px/1.5 "proxima-nova", Helvetica, Arial, sans-serif;
  background: #fff;
  color: #363636;
  margin: 0;
  padding: 0;
  height: 100%;
