    --wp--preset--font-size--medium:25px;
  }
}
@media only screen and (max-width: 1024px) {
  body {
    --wp--preset--spacing--1: 0.5rem;
    --wp--preset--spacing--2: 1rem;
    --wp--preset--spacing--3: 1.5rem;
    --wp--preset--spacing--4: 2rem;
    --wp--preset--spacing--5: 2.5rem;
    --wp--preset--spacing--6: 3rem;
    --wp--preset--spacing--7: 5rem;
    --wp--preset--spacing--8: 4rem;
    --wp--preset--spacing--9: 4.5rem;
    --wp--preset--spacing--10: 5rem;
    --wp--preset--spacing--11: 75px;
    --wp--preset--spacing--12: 6rem;
    --wp--preset--spacing--13: 6.5rem;
    --wp--preset--spacing--14: 7rem;
