.wp-block-button .wp-element-button,
.btn {
  display: inline;
  justify-content: center;
  align-items: center;
  vertical-align: top;
  font-size: 18px;
  line-height: 100%;
  padding: 10px 13px;
  text-decoration: none;
  background-color: var(--global-color-main);
  color: #FFF;
  cursor: pointer;
  position: relative;
  font-weight: 700;
  font-family: "proxima-nova", Helvetica, Arial, sans-serif;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  transition: all 300ms linear;
  text-transform: capitalize;
  border: 2px solid var(--global-color-main);
  border-radius: 6px;
}
.wp-block-button .wp-element-button:hover,
.btn:hover {
  transition: all 300ms linear;
  background-color: #FFF;
  color: var(--global-color-main);
}
@media only screen and (max-width: 1024px) {
  .wp-block-button .wp-element-button,
  .btn {
    font-size: 18px;
  }
}
.wp-block-button .wp-element-button__text,
.btn__text {
  position: relative;
}
.wp-block-button .wp-element-button__icon,
.btn__icon {
  position: relative;
  line-height: 1;
  transition: all 300ms linear 0s;
}
.wp-block-button .wp-element-button__icon--after,
.btn__icon--after {
  order: 2;
}
.wp-block-button .wp-element-button__icon--before,
.btn__icon--before {
  order: -1;
}
.wp-block-button .wp-element-button__icon:hover,
.btn__icon:hover {
  transition: all 300ms linear;
}
.wp-block-button .wp-element-button__icon svg,
.btn__icon svg {
  fill: currentColor;
  vertical-align: top;
}
.wp-block-button .wp-element-button__icon img,
.btn__icon img {
  width: 30px;
  height: 30px;
  object-fit: contain;
  vertical-align: top;
}
.wp-block-button .wp-element-button--icon,
.btn--icon {
  padding-right: 8px;
}
.wp-block-button .wp-element-button--icon .svg-icon,
.btn--icon .svg-icon {
  width: 28px;
  height: 28px;
  padding: 9px 7px;
  border: 1px solid;
